const MyMessage = (messages) => {
  if (messages?.attechment?.length > 0) {
    return (
      <img
        src={messages.attechment[0].file}
        alt="message attchment"
        className="message-image"
        style={{ float: "right" }}
      />
    );
  }
  return (
    <div
      className="message"
      style={{
        float: "right",
        marginRight: "18px",
        color: "white",
        backgroundColor: "#3B2A50",
      }}
    >
      {messages.text}
    </div>
  );
};

export default MyMessage;
